import { Button, Form, Input, Tooltip, notification } from "antd";

import {
  UserOutlined,
  QuestionCircleOutlined,
  SmileOutlined,
  MehOutlined,
} from "@ant-design/icons";
import { useState } from "react";

function SignIn({ onCompleteSignIn }: { onCompleteSignIn: () => void }) {
  const [isNotSame, setIsNotSame] = useState<boolean>(false);
  const ID = "id";
  const PASSWORD = "password";

  const openNotification = () => {
    notification.open({
      message: " !",
      description: "  !",
      icon: <SmileOutlined style={{ color: "#a0d911" }} />,
      onClose: () => {
        window.location.href =
          "https://github.com/blcklamb/RESAT_FarmingChallenge";
      },
    });
  };

  const openFailNotification = () => {
    notification.open({
      message: " !",
      description: "  ",
      icon: <MehOutlined style={{ color: "#f5222d" }} />,
    });
  };

  const onFinish = (values: any) => {
    const { id: enteredId, password: enteredPW } = values;
    console.log(enteredId, enteredPW);
    if (enteredId === ID && enteredPW === PASSWORD) {
      openNotification();
      onCompleteSignIn();
    } else {
      openFailNotification();
      setIsNotSame(true);
    }
  };

  const IDLabel = () => {
    return (
      <>
         ID
        <Tooltip
          placement="rightTop"
          title={` ${ID}.`}
          arrow={true}
        >
          <div style={{ marginLeft: "8px" }}>
            <QuestionCircleOutlined />
          </div>
        </Tooltip>
      </>
    );
  };

  const PWLabel = () => {
    return (
      <div
        style={{
          display: "flex",
          alignItems: "center",
        }}
      >
         PASSWORD
        <Tooltip
          placement="rightTop"
          title={` ${PASSWORD}.`}
          arrow={true}
        >
          <div style={{ marginLeft: "8px" }}>
            <QuestionCircleOutlined />
          </div>
        </Tooltip>
      </div>
    );
  };

  return (
    <Form
      name="basic"
      labelCol={{ span: 8 }}
      wrapperCol={{ span: 16 }}
      style={{
        marginTop: "50px",
        margin: "20px",
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
      }}
      onFinish={onFinish}
      autoComplete="off"
      layout="vertical"
    >
      <>
        <Form.Item
          label={<IDLabel />}
          name="id"
          hasFeedback={isNotSame}
          validateStatus={isNotSame ? "error" : ""}
          rules={[{ required: true, message: " ." }]}
          style={{ minWidth: "290px" }}
        >
          <div style={{ width: "290px" }}>
            <Input suffix={<UserOutlined className="site-form-item-icon" />} />
          </div>
        </Form.Item>
        <Form.Item
          label={<PWLabel />}
          name="password"
          hasFeedback={isNotSame}
          validateStatus={isNotSame ? "error" : ""}
          rules={[{ required: true, message: " ." }]}
        >
          <div style={{ width: "290px" }}>
            <Input.Password />
          </div>
        </Form.Item>
      </>
      <Form.Item wrapperCol={{ span: 16 }}>
        <Button type="primary" htmlType="submit">
          
        </Button>
      </Form.Item>
    </Form>
  );
}

export default SignIn;
